import whatsappIcon from "assets/images/whatsapp.webp";
import { useTranslation } from "react-i18next";
import Styles from "./index.module.css";

const openWhatsapp = (message: string) => () => {
  const url = `https://api.whatsapp.com/send?phone=573125256655&text=${message}`;
  window?.open(url, "_blank")?.focus();
};

interface Props {
  position: "left" | "right";
}

export const WhatsappButton = (props: Props) => {
  const { t } = useTranslation();

  const classes = [Styles.whatsappButton];
  if (props.position === "right") {
    classes.push(Styles.right);
  } else {
    classes.push(Styles.left);
  }
  return (
    <button className={classes.join(" ")} onClick={openWhatsapp(t("landing.whatsapp_message"))}>
      <img alt="whatsapp icon" src={whatsappIcon} />
      <label>{t("landing.whatsapp_button")}</label>
    </button>
  );
};
