import { CircularProgress } from "@mui/material";
import { PlatformModuleHeader } from "components";
import { GalacticWallpaper, GoToFeedButtonWrapper, PlatformLayout } from "hoc";
import { Event } from "models";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { eventService } from "services";
import { EventSettings } from "./event-settings";
import { Table } from "./table";

export const AdminEvent = () => {
  const { t } = useTranslation();
  const [event, setEvent] = useState<Event | undefined>();
  useEffect(() => {
    (async () => {
      const response = await eventService.getEvent();
      setEvent(response.data);
    })();
  }, []);

  let settings = (
    <div className="center">
      <CircularProgress />
    </div>
  );

  if (event) {
    settings = <EventSettings event={event} />;
  }

  return (
    <GalacticWallpaper>
      <GoToFeedButtonWrapper>
        <PlatformLayout>
          <PlatformModuleHeader label={t("event.event_rooms")} />
          <Table eventId={event?.eventId as string} />
          <PlatformModuleHeader label={t("event.settings")} />
          {settings}
        </PlatformLayout>
      </GoToFeedButtonWrapper>
    </GalacticWallpaper>
  );
};
