import { CircularProgress } from "@mui/material";
import { GameButton } from "components";
import { GalacticColors } from "enums/galactic-backgrounds";
import { UserConfigurationButtonWrapper } from "hoc";
import { useState } from "react";
import { useTranslation } from "react-i18next";
import { DeleteEvent } from "../delete-event";
import { DeleteRoom } from "../delete-room";
import Styles from "./index.module.css";
import { createNewRoom } from "./utils";
import { createNewEvent } from "./utils/create-new-event";

export const CreateRoom = () => {
  const { t } = useTranslation();
  const [isLoading, setLoading] = useState(false);
  const [openRoomErrorDialog, setOpenRoomErrorDialog] = useState(false);
  const [openEventErrorDialog, setOpenEventErrorDialog] = useState(false);

  let gameButton = (
    <div className={Styles.buttonContainer}>
      <GameButton
        dataTestId="create-room-button"
        onClick={createNewRoom(t, setLoading, setOpenRoomErrorDialog)}
        backgroundImage={GalacticColors.BLUE}
        label={t("rooms.create_a_room")}
      />
      <GameButton
        dataTestId="create-event-button"
        onClick={createNewEvent(t, setLoading, setOpenEventErrorDialog)}
        backgroundImage={GalacticColors.BLUE}
        label={t("events.create_an_event")}
      />
    </div>
  );

  if (isLoading) {
    gameButton = <CircularProgress />;
  }

  return (
    <UserConfigurationButtonWrapper>
      {gameButton}
      <DeleteRoom
        createRoom={createNewRoom(t, setLoading, setOpenRoomErrorDialog)}
        open={openRoomErrorDialog}
        setOpen={setOpenRoomErrorDialog}
      />
      <DeleteEvent
        createEvent={createNewEvent(t, setLoading, setOpenEventErrorDialog)}
        open={openEventErrorDialog}
        setOpen={setOpenEventErrorDialog}
      />
    </UserConfigurationButtonWrapper>
  );
};
