import { UserLicenseAssignment } from "models";
import { useTranslation } from "react-i18next";
import Styles from "./index.module.css";

interface Props {
  users?: Array<UserLicenseAssignment>;
  totalAdmin?: number;
}

export const AdminCount = ({ totalAdmin, users }: Props) => {
  const { t } = useTranslation();
  if (!users) {
    return null;
  }
  const total = users.filter(u => u.isAdmin).length + 1;
  return (
    <div>
      <label className={Styles.title}>{t("user_plan_management.active_admins")}</label>
      <label className={Styles.subtitle}>
        {total} / {totalAdmin || ''}
      </label>
    </div>
  );
};
