import { BrowserValidator, OnlyAdminWrapper, TokenExtractor } from "hoc";
import { AdminEvent, BillsView, UserPlanManagement, Event } from "pages/platform";
import RoomPage from "pages/platform/room/room.page";
import RoomsPage from "pages/platform/rooms/rooms.page";
import UserAccount from "pages/platform/user-account/user-account";
import { Route, Routes } from "react-router-dom";
import { Routing } from "routing";

const Platform = () => (
  <TokenExtractor>
    <BrowserValidator>
      <Routes>
        <Route path={Routing.platform.rooms} element={<RoomsPage />} />
        <Route path={`${Routing.platform.room}/:pin`} element={<RoomPage />} />
        <Route path="account" element={<UserAccount />} />
        <Route
          path="user-plan-management"
          element={
            <OnlyAdminWrapper>
              <UserPlanManagement />
            </OnlyAdminWrapper>
          }
        />
        <Route
          path="bills"
          element={
            <OnlyAdminWrapper>
              <BillsView />
            </OnlyAdminWrapper>
          }
        />
        <Route
          path="admin/event"
          element={
            <OnlyAdminWrapper>
              <AdminEvent />
            </OnlyAdminWrapper>
          }
        />
        <Route path="event/:pin" element={<Event />} />
      </Routes>
    </BrowserValidator>
  </TokenExtractor>
);

export default Platform;
