import { CircularProgress } from "@mui/material";
import { GalacticWallpaper, GoToFeedButtonWrapper, PlatformLayout } from "hoc";
import { useEffect } from "react";
import { useParams } from "react-router-dom";
import { eventService } from "services";
import { goToRoom } from "utils";

export const Event = () => {
  const { pin } = useParams();
  useEffect(() => {
    (async () => {
      if (pin) {
        const event = await eventService.getEventRoom(pin);
        console.log(event);
        const roomId = event.data.pin;
        goToRoom(roomId)();
      }

    })();
  }, [pin]);
  return (
    <GalacticWallpaper isContentHorizontallyCentered={true} isContentVerticallyCentered={true}>
      <GoToFeedButtonWrapper>
        <PlatformLayout>
          <CircularProgress />
        </PlatformLayout>
      </GoToFeedButtonWrapper>
    </GalacticWallpaper>
  );
};
