import { Button, Dialog, DialogActions, DialogTitle } from "@mui/material";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { roomService } from "services";
import { goToRoom } from "utils";

interface Props {
  open: boolean;
  setOpen: (open: boolean) => void;
  createRoom: () => Promise<void>;
}

export const DeleteRoom = (props: Props) => {
  const { t } = useTranslation();
  const [roomId, setRoomId] = useState('');

  const recreateRoom = async () => {
    await roomService.deleteRoom();
    await props.createRoom();
    props.setOpen(false);
  };

  const cancel = () => props.setOpen(false);

  useEffect(() => {
    (async () => {
      const room = await roomService.getRoom();
      setRoomId(room?.data?.pin);
    })();
  }, []);

  return (
    <Dialog open={props.open}>
      <DialogTitle>{t("errors.user_has_an_active_room")}</DialogTitle>
      <DialogActions>
        <Button onClick={goToRoom(roomId)}>{t("room.go_room")}</Button>
        <Button onClick={cancel}>{t("common.no")}</Button>
        <Button onClick={recreateRoom} autoFocus>
          {t("common.yes")}
        </Button>
      </DialogActions>
    </Dialog>
  );
};
