import { Box, Modal, Tab, Tabs } from "@mui/material";
import { GameButton, TabPanel } from "components";
import { GalacticColors } from "enums/galactic-backgrounds";
import { sanitizeLanguage } from "lang/i18";
import { SyntheticEvent, useState } from "react";
import { useTranslation } from "react-i18next";
import ReactPlayer from "react-player";
import { modalStyle } from "styles";
import { a11yProps } from "utils";
import Styles from "./index.module.css";
import { sections } from "./sections";
import { environment } from "environment";

interface Props {
  open: boolean;
  close: () => void;
}

export const LearningModal = ({ open, close }: Props) => {
  const [value, setValue] = useState(0);
  const { t, i18n } = useTranslation();
  const handleChange = (_event: SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };

  const video = `${environment.landingUrl}/assets/videos/tutorials/${
    sections[value].key
  }/${sanitizeLanguage(i18n.language)}.mp4`;

  return (
    <Modal open={open}>
      <Box sx={modalStyle}>
        <h1 className={`${Styles.title} neon`}>{t("rooms.faq")}</h1>
        <div>
          <Box sx={{ flexGrow: 1, bgcolor: "background.paper", display: "flex", height: 300 }}>
            <Tabs
              orientation="vertical"
              variant="scrollable"
              onChange={handleChange}
              value={value}
              aria-label="Vertical tabs example"
              sx={{ borderRight: 1, borderColor: "divider" }}
            >
              {sections.map((section, index) => (
                <Tab key={section.title} label={t(section.title)} {...a11yProps(index)} />
              ))}
            </Tabs>
            <TabPanel key={sections[value].title} className={Styles.container} value={0} index={0}>
              <ReactPlayer
                width={"calc(100% - 15px)"}
                height={"100%"}
                style={{ marginLeft: "15px", maxWidth: "calc(50vw - 15px)" }}
                url={video}
                playing={false}
                controls={true}
                loop={false}
                playsinline={true}
              />
            </TabPanel>
          </Box>
        </div>
        <div className="center">
          <GameButton
            className={Styles.closeButton}
            onClick={close}
            backgroundImage={GalacticColors.BLUE}
            label={t("common.close")}
          />
        </div>
      </Box>
    </Modal>
  );
};
